/* =================================MENUBAR */

.menubar__wrapper {
  font-size: 20px;

  margin: 0 5px 0 0;
  padding: 10px 10px;
  height: fit-content;
  max-width: 190px;
}

.menuBar__list li {
  min-width: 150px;
  margin: 0 0 5px 0;
  padding: 0 30px 0 30px;
}

.menuBar__list p {
  position: relative;
  padding: 0 0 0 30px;

}

.menuBar__list img {
  display: inline-block;
  position: absolute;
}